@font-face {
  font-family: 'faktNormal';
  src: local('faktNormal'), url(./assets/fonts/Fakt-Normal.otf) format('otf');
}

*,
*::after,
*::before {
  box-sizing: border-box;
  font-family: 'faktNormal', sans-serif;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0 0 0 0;
    padding: 0 0 0 0;
    border: 0;
    outline: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
    font-family: 'faktNormal', sans-serif;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
    display: block;
}
body {
    line-height: 1;
}
ol, ul {
    list-style: none;
}
blockquote, q {
    quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
    content: '';
    content: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}

.nav {
  display: flex;
  flex-direction: row;
  height: 10vh;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
}

.nav-itens {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 8px 0 8px;
}
.content {
  height: 100vh;
  width: 80%;
  margin: auto;
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
}

.primary-bg {
  background-color: #20c997;
}

.secondary-bg {
  background-color: #e7edea;
}

.green-bg {
  background-color: #0abf53;
}

.row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
}

.recorder-box {
  padding: 32px;
  align-items: center;
  justify-content: center;
  width: 510px;
  height: 480px;
  background-color: #0abf53;
  border-radius: 8px;
  margin: 0 16px 0 0;
}

.button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 60px;
  border-radius: 8px;
  background-color: #FFF;
  min-width: 100%;
  color: #00112c;
  font-weight: bold;
  font-size: 28px;
  margin: 16px 0 16px 0;
}

.box-text {
  background-color: rgba( 0, 0, 0, 0.2);
  margin-top: 16px;
  padding: 34px;
  border-radius: 8px;
  min-height: 80px;
  color: #FFF;
  font-size: 22px;
}

.input-doc {
  background-color: #FFF;
  min-width: 100%;
  height: 60px;
  border-radius: 8px;
  padding: 8px;
  font-size: 22px;
  font-family: sans-serif;
}